<template>
  <el-form-item
    v-bind="schema"
    :prop="vkey"
    :label="schema.label"
    :rules="schema.rules"
  >
    <component
      v-bind="mergeAttrs"
      v-model="formatValue"
      :is="componentFormat"
      :vkey="vkey"
      :schema="schema"
    ></component>
  </el-form-item>
</template>

<script>
import { T } from "../../utils/index";
export default {
  name: "YoSchemaFormItem",
  props: {
    value: {
      required: true,
    },
    vkey: {
      type: String,
      required: true,
    },
    schema: {
      type: Object,
      required: true,
    },
  },
  computed: {
    // ÂÄºËΩ¨Êç¢ÔºåÂº∫Âà∂Â≠êÁªÑ‰ª∂‰øÆÊîπÁà∂Á∫ßÁªÑ‰ª∂ÂÄº
    // Âç≥ ÂèØ‰ª•‰ΩøÁî® this.formatValue = xxx
    formatValue: {
      get() {
        return this.value;
      },
      set(val) {
        this.$emit("input", val);
      },
    },
    // ÂêàÂπ∂ Attrs ‰æø‰∫éËá™ÂÆö‰πâÁªÑ‰ª∂attrsËÆæÁΩÆ
    mergeAttrs() {
      if (this.schema.attrs && T(this.schema.attrs) === "object") {
        return {
          ...this.$attrs,
          ...this.schema.attrs,
        };
      }
      return this.$attrs;
    },
    // Ê†ºÂºèÂåñÁªÑ‰ª∂ÂêçÁß∞
    // 1„ÄÅÂà§Êñ≠schema‰∏≠ÊòØÂê¶ÊåáÂÆöÁöÑÁªÑ‰ª∂ÔºåÊåáÂÆö‰∫ÜÂàôÁõ¥Êé•ËøîÂõûÁªÑ‰ª∂ÂêçÁß∞
    // 2„ÄÅschemaÊú™ÊåáÂÆöÁªÑ‰ª∂ÂêçÁß∞ÔºåÂàôÊ†πÊçÆschema‰∏≠ÊåáÂÆöÁöÑtype ÂåπÈÖçÂÜÖÁΩÆÁªÑ‰ª∂
    // 3„ÄÅschemaÊú™ÊåáÂÆötype ÂàôÂà§Êñ≠ ÂΩìÂâçvalueÂÄºÁ±ªÂûã ÊåáÂÆö ÂåπÈÖçÁªÑ‰ª∂
    // 4„ÄÅÂ¶ÇÊú™ÂåπÈÖçÂà∞ÁªÑ‰ª∂ Âàô Ê∏≤Êüì input ÈªòËÆ§ÁªÑ‰ª∂
    componentFormat() {
      if (this.schema && this.schema.component) {
        return this.schema.component;
      }
      let MODELTYPE = "";
      if (this.schema && this.schema.type) {
        MODELTYPE = this.schema.type;
      } else {
        MODELTYPE = T(this.formatValue);
      }
      switch (MODELTYPE) {
        case "number":
          return "el-input-number";
        case "array":
          return "yo-schema-array";
        case "boolean":
          return "yo-schema-boolean";
        case "object":
          return "yo-schema-object";
      }
      return "el-input";
    },
  },
};
</script>
