import { T } from "./index";

const NotMatchedTypeValue = "<yo-schema-empty />";

// Ê†πÊçÆÊ®°ÊùøÂÄºÁ±ªÂûã‰øÆÊ≠£Êï∞ÊçÆ
// 1„ÄÅÂà§Êñ≠‰º†ÂÖ•ÁöÑÂÄºÁ±ªÂûãÊòØÂê¶‰∏éÊ®°ÊùøÁ±ªÂûã‰∏ÄËá¥
// 2„ÄÅÂ¶Ç‰∏ÄËá¥ÂàôÁõ¥Êé•ËµãÂÄºÔºåÂê¶ÂàôÊ†πÊçÆÊ®°ÊùøÂÄºÁ±ªÂûãËµãÈªòËÆ§ÂÄº
const RecursionValueBinder = (value, schema) => {
  const Tvalue = T(value);

  if (schema.type === "boolean") {
    if (Tvalue === schema.type) {
      return value;
    }
    return false;
  }

  if (schema.type === "string") {
    if (Tvalue === schema.type) {
      return value;
    }
    return "";
  }

  if (schema.type === "number") {
    if (Tvalue === schema.type) {
      return value;
    }
    return 0;
  }

  if (schema.type === "array") {
    if (Tvalue === schema.type) {
      const data = [];
      for (let i = 0; i < value.length; i++) {
        const val = value[i];
        const res = RecursionValueBinder(val, schema.items);
        if (res !== NotMatchedTypeValue) {
          data.push(res);
        }
      }
      return data;
    }
    return [];
  }

  if (schema.type === "object") {
    const inner = {};
    for (let key in schema.properties) {
      // Âà§Êñ≠valuesÊòØÂê¶Â≠òÂú®ÂÄº
      if (value && value[key]) {
        // value [key]ÂèØËÉΩÂá∫Áé∞ undefined Áé∞Ë±°ÔºåÂåπÈÖç‰∏çÂà∞ Âàô‰ºöËøîÂõûÈªòËÆ§ÂÄº
        inner[key] = RecursionValueBinder(value[key], schema.properties[key]);
      } else {
        inner[key] = RecursionValueBinder("", schema.properties[key]);
      }
    }
    if (Object.keys(inner).length) {
      return inner;
    }

    // Â¶ÇËøîÂõûÁªìÊûúÊ≤°Êúâ ÂåπÈÖçÂà∞ number string boolean object array
    // ÂàôÁõ¥Êé•ËøîÂõû Á©∫(‰æãÂ¶Ç Symbol„ÄÅNull„ÄÅUndefined, Ëøô‰∫õÊï∞ÊçÆÁ±ªÂûãÁöÑÂÄº ÂØπ‰∫éÊ®°ÊùøË°®ÂçïÊù•ËÆ≤Âπ∂Ê≤°ÊúâÊÑè‰πâÔºåÁî®Êà∑‰πüÊó†Ê≥ïÁõ¥Êé•ËæìÂÖ•Ëøô‰∫õÁ±ªÂûãÁöÑÂÄº)
    // ÂêåÊó∂ ËøôÈáåÈÅøÂÖç‰∫Ü return {} ÁöÑÊÉÖÂÜµ
    return NotMatchedTypeValue;
  }

  return NotMatchedTypeValue;
};

/**
 * @brief Ê®°Êùø‰∏éÂÄºÊï∞ÊçÆ‰øÆÊ≠£ÂèäÊ†°È™å
 * @param models {object}
 * @param schemas {object}
 */
export default function ValueTransformWithSchema(models, schemas) {
  // const models = deepmerge.all([values]);
  // const schemas = deepmerge.all([sches]);
  // const models = values
  const data = {};
  for (let key in schemas) {
    const model = models[key];
    const schema = schemas[key];
    const result = RecursionValueBinder(model, schema);
    if (result !== NotMatchedTypeValue) {
      data[key] = result;
    }
  }
  return data;
}
