<template>
  <el-form v-loading="!valueOk" ref="form" :model="value" v-bind="$attrs">
    <template v-if="valueOk">
      <template v-for="(obj, key) in value">
        <YoSchemaFormItem
          v-model="value[key]"
          :model="value[key]"
          :key="key"
          :vkey="key"
          :prop="key"
          :schema="schema[key]"
        />
      </template>
    </template>
    <slot></slot>
  </el-form>
</template>

<script>
import ValueTransformWithSchema from "../../utils/ValueTransformWithSchema";
import { T } from "../../utils/index";
export default {
  name: "YoSchemaForm",
  props: {
    value: {
      required: true,
    },
    schema: {
      type: Object,
      default() {
        return {};
      },
    },
  },
  provide: {
    yoschemaform() {
      return this;
    },
  },
  watch: {
    schema: {
      deep: true,
      immediate: true,
      handler() {
        // ÁõëÂê¨schemaÂèòÂåñÔºåÈáçÊñ∞Ê†°È™åÂÄº
        this._valueTransformWithSchema();
      },
    },
  },
  data() {
    return {
      // ÈÅøÂÖçÂÄºËøòÊú™‰øÆÊ≠£ Â∞±ÂºÄÂßãÊ∏≤ÊüìÔºåÂØºËá¥Ëé∑Âèñschema‰∏çÂ≠òÂú®ÁöÑÂ±ûÊÄßÊä•ÈîôÈóÆÈ¢ò
      valueOk: false,
    };
  },
  methods: {
    // Â∞Ü‰º†ÂÖ•ÁöÑÂÄºÊ†πÊçÆschemaËøõË°åËΩ¨Êç¢‰∏éÂÄºÊ†°È™å
    _valueTransformWithSchema() {
      this.valueOk = false;
      if (T(this.value) !== "object") {
        console.error("value or v-model prop should be an object");
        return;
      }
      if (T(this.schema) !== "object") {
        console.error("schema prop should be an object");
        return;
      }
      const datas = ValueTransformWithSchema(this.value, this.schema);
      this.$emit("input", datas);
      // ÈÅøÂÖçÂÄºËøòÊú™‰øÆÊ≠£ Â∞±ÂºÄÂßãÊ∏≤ÊüìÔºåÂØºËá¥Ëé∑Âèñschema‰∏çÂ≠òÂú®ÁöÑÂ±ûÊÄßÊä•ÈîôÈóÆÈ¢ò
      this.$nextTick(() => {
        this.valueOk = true;
      });
    },
    // Ë°®ÂçïÈ™åËØÅ
    validate(cb) {
      if (!cb && cb.constructor !== Function) {
        console.log("Invalid Callback Function");
        return;
      }
      this.$refs.form.validate((valid) => {
        cb(valid);
      });
    },
  },
};
</script>
<style lang="scss" scoped></style>
